.p {
  color: #ffffff;
  font-size: 24px;
  font-weight: lighter;
  display: block;
  text-align: center;
  line-height: 40px;
  width: 464px;
  margin-left: 118px;
  margin-top: 0px;
  margin-bottom: 20px;
}

.h1 {
  color: white;
  font-size: 64px;
  font-weight: lighter;
  display: block;
  text-align: center;
  line-height: 76px;
  margin: 43px 0;
}

.button {
  margin: 20px;
  padding: 15px 15px;
  text-align: center;
  text-transform: uppercase;
  transition: 0.5s;
  background-size: 200% auto;
  color: white;
  border-radius: 10px;
  display: block;
  border:0px;
  font-weight: 100;
  box-shadow: 0px 0px 14px -7px #8b85c1;
  background-image: linear-gradient(45deg, #993955 0%, #8b85c1 51%, #993955 100%);
  cursor: pointer;
  user-select: none;
  -webkit-user-select: none;
  touch-action: manipulation;
  width: 314px;
  height: 67px;
  font-size: 32px;
  line-height: 10px;
  alignment: center;

}

.button:hover {
  background-position: right center;
  color: #fff;
  text-decoration: none;
}

.button:active {
  transform: scale(0.95);
}

.App {
  background-color: #37123C;
  width: 700px;
  height: 500px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  border-radius: 16px;
}

.div_button {
  display: grid;
  place-content: center;
  border-bottom-width: 400px;
}